body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.game-info {
    margin-left: 20px;
}

/*my define*/

.content{
    width: 1000px;
    margin: 20px auto;
    border: #000 solid 1px;
    background-color: #c4f5ff;
}

#root{
    margin: 10px 10px;
}

.input-content{
    background-color: #b21f2d;
    margin-bottom: 10px;
}

.input-content>span{
    color: white;
    margin-left: 10px;
}

button.btn-secondary, .input-group-btn{
    background-color: #ffd114;
    height: 20px;
}

button{
    border: black 1px solid;
}

.arrange-btn{
    margin: 10px 10px;
    height: 30px;
    background-color: #e25412;
    color: white;
    border-radius: 4px;
}

input{
    border: none;
    height: 20px;
    padding: 0
}

.form-control{
    margin: 10px 0 10px 10px;
    width: 80px;
}

.game-info{
    border: black solid 2px;
    width: 200px;
}

.text-red{
    /*background-color: red;*/
    color: #ff2c3c;
}

.bg-green{
    /*background-color: red;*/
    background-color: green;
    color: white;
}

.bg-yellow{
    color: black;
    background-color: yellow;
}

.status{
    margin: 10px 10px;
    font-weight: bold;
}

.game{
    margin: 10px auto;
}

.game-info, .game-board{
    margin: 10px auto;
}



